.App {
  margin: 20px 0;
  text-align: center;
}
.key {
  padding: 5px 10px;
  outline: none;
  margin-bottom: 20px;
  border: 1px solid #ebebeb;
}
.file{
  outline: none;
  padding: 5px;
  background-color: #ebebeb;
  margin-bottom: 20px;
}
.image{
  max-width: 98%;
  -moz-box-shadow: 2px 3px 5px rgb(100,100,100);
  -webkit-box-shadow: 2px 3px 5px rgb(100,100,100);
  box-shadow: 2px 3px 5px rgb(100,100,100);
}